body {
  color: var(--text-dark-color);
  background-color: var(--background-light-color);
  font-family: Montserrat, sans-serif;
}

:root {
  --text-dark-color: #111;
  --text-light-color: #fff;
  --text-orange-color: #fd9222;
  --text-dark-transparent-color: #111111b3;
  --text-light-transparent-color: #ffffffb3;
  --text-label-dark-transparent-color: #4040404d;
  --icon-dark-color: #111;
  --icon-dark-transparent-color: #1111111a;
  --icon-orange-color: #fd9222;
  --background-dark-color: #1e1823;
  --background-light-color: #fafafa;
  --background-white-color: #fff;
  --background-orange-color: #fd9222;
  --background-button-color: #fcf1e0;
  --background-modal-light-color: #fafafa;
  --border-dark-transparent-color: #ffffff80;
  --transition-duration: .25s;
  --transition-tf: cubic-bezier(.4, 0, .2, 1);
}

* {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  display: block;
}

.link {
  text-decoration: none;
}

.list {
  list-style: none;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0;
}

:root {
  --text-dark-color: #111;
  --text-light-color: #fff;
  --text-orange-color: #fd9222;
  --text-dark-transparent-color: #111111b3;
  --text-light-transparent-color: #ffffffb3;
  --text-label-dark-transparent-color: #4040404d;
  --icon-dark-color: #111;
  --icon-dark-transparent-color: #1111111a;
  --icon-orange-color: #fd9222;
  --background-dark-color: #1e1823;
  --background-light-color: #fafafa;
  --background-white-color: #fff;
  --background-orange-color: #fd9222;
  --background-button-color: #fcf1e0;
  --background-modal-light-color: #fafafa;
  --border-dark-transparent-color: #ffffff80;
  --transition-duration: .25s;
  --transition-tf: cubic-bezier(.4, 0, .2, 1);
}

.header-page {
  background-color: var(--background-orange-color);
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 30px;
  margin: 10px auto 20px;
  display: flex;
}

:root {
  --text-dark-color: #111;
  --text-light-color: #fff;
  --text-orange-color: #fd9222;
  --text-dark-transparent-color: #111111b3;
  --text-light-transparent-color: #ffffffb3;
