/* @@@@@@@@@@@@@@@@@@@@@ Mobile @@@@@@@@@@@@@@@@@@@@ */
@media screen and (max-width: 767px) {
  .container {
    max-width: 375px;
  }
  .subscribe-header-mobile {
    color: #fff;
    text-align: center;
    font-family: Montserrat;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: 1.2; /* we wsztstkich line-height dalem wartość wynik =line-heigth/font-size */
    letter-spacing: -0.6px;
    width: 291px;
    padding-top: 62px;
    margin-left: auto;
    margin-right: auto;
  }
  .subscribe-paragraph-mobile {
    color: #fff;
    text-align: center;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 1.28; /* 128.571% */
    letter-spacing: -0.28px;
    width: 326px;
    padding: 24px;
    margin-left: auto;
    margin-right: auto;
  }
  .subscribe-button {
    color: #fff;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    border-radius: 100px;
    background: #fd9222;
    border: none;
    padding: 12px 28px;
    display: block;
    margin-left: auto;
    margin-right: auto;
  }

  .subscribe-section-mobile {
    background-image: url(../images/subscribe/subcribe-background-chocolate-375x370-mobile.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    padding-bottom: 63px;
  }
  @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    .subscribe-section-mobile {
      background-image: url(../images/subscribe/subcribe-background-chocolate-750x740-mobile@2x.jpg);
    }
  }

  /* modal */
  .subscribe-modal-window.is-hidden {
    opacity: 0;
    pointer-events: none;
  }
  .modal-image-container {
    width: 311px;
    height: 169px;
    background-image: url(../images/explore-our-new-chocolate-first-modal/chocolate-modal-background-311x169-mobile.jpg);
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
  }

  @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    .modal-image-container {
      background-image: url(../images/explore-our-new-chocolate-first-modal/chocolate-modal-background-622x338-mobile@2x.jpg);
      background-size: cover;
    }
  }
  .subscribe-modal-header {
    color: #111;
    font-family: Montserrat;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: 1.2; /* 120% */
    letter-spacing: -0.6px;
    text-transform: uppercase;
    width: 263px;
    height: 48px;
    padding-top: 32px;
    padding-bottom: 24px;
    margin-left: 24px;
  }
  .subscribe-form {
    display: block;
    margin-top: 35px;
  }
  .subscribe-span {
    color: #fd9222;
    font-family: Montserrat;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: 1.2;
    letter-spacing: -0.6px;
    text-transform: uppercase;
    padding-right: 5px;
  }

  .subscribe-modal-window {
    position: fixed;
    inset: 0 0 auto;
    min-height: 100vh;
    padding: 72px 24px 24px 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 1;
    pointer-events: all;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: white;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
      visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }
  .subscibe-image-modal {
    display: inline-block;
    border: 1px black solid;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
  }
  .subscribe-modal-button {
    color: #fff;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    border-radius: 100px;
    background: #fd9222;
    border: none;
    padding: 12px 28px;
    margin-left: auto;
    margin-right: auto;
  }
  .subscribe-modal-form-button {
    border-radius: 100px;
    border: none;
    background: #fd9222;
    padding: 14px 28px;
    position: absolute;
    right: 22px;
    color: #fff;
    text-align: center;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    width: 100px;
  }
  .subscribe-form-modal-container {
    position: relative;
    margin-left: auto;
    margin-right: auto;
  }
  .subscribe-input-email {
    position: relative;
    color: rgba(64, 64, 64, 0.3);
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: -0.28px;
    border-radius: 25px;
    border: 1.5px solid rgba(17, 17, 17, 0.05);
    box-shadow: 0px 5.44492px 5.44492px 0px rgba(0, 0, 0, 0.03);
    padding: 14px 18px;
    width: 263px;
    margin-left: 24px;
  }
  .subscribe-modal-icon {
    width: 14px;
    height: 14px;
  }

  .subscribe-close-button {
    border: none;
    fill: white;
    background-color: transparent;
    border: none;
    position: relative;
    left: 278px;
    bottom: 148px;
  }
  .subscribe-close-button:hover,
  .subscribe-close-button:focus {
    fill: #fd9222;
  }
}
